.show{
    display: block !important;
    transition: all 0.5s ease;
 }









 

.accordion-item__body{
    display: none;
    padding: 5px;
    font-size: 16px;
}


 .accordion-item{

    border-bottom: 1px solid #FFFFFF;
    
 }

 
.accordion-item__header{
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 530px;
    width: 100%;
     
    font-size:16px;
    min-height: 60px;
    padding: 15px 5px;
    gap: 12px;
    position: relative;
    z-index: 5;
}
span.accordion-item__icon{
    font-size: 24px;
}
 


 