.map-preloader{
    width:inherit;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0,0.8);
    z-index: 1100;

    flex-direction: column;
    gap: 20px;
}
.map-preloader__text{
    color:white;
    font-size: 40px;
    display: flex;
    flex-direction: column;
    gap: 5px;

}

