a {
	color: inherit;
	text-decoration: none
}
 
 

.main-page__container {
	margin-bottom: 70px;
}

.main-page__container .main-page__title {
	text-align: center;
	margin-bottom: 40px;
}

.cards-group {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 20px;
}

.small-card-group {
	display: flex;
	justify-content: space-between;
	flex-wrap: nowrap;
	gap: 20px;
}

.card-ws__container {
	display: flex;
	flex-wrap: nowrap;
	justify-content: center;
	gap: 20px;
}

.main-page__rating-list {
	max-width: 350px;
	width: 100%;
	background: #fff;
	border: 1px solid silver;
}

.main-page__title {
	font-family: 'Open-Sans';
	font-weight: 700;
	font-size: 28px;
}

.modal-card {
	max-width: 250px;
	width: 100%;
	min-height: 170px;
	background: #fff;
	/* box-shadow: 0 4px 4px rgba(0, 0, 0, .25); */
	box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
	 
	border-radius: 10px;
	padding-bottom: 25px;
	transition:all 0.3s ease-in-out ;
}
.modal-card:hover {
	transform: translateY(-0.5rem) scale(1.0125);
	box-shadow: 0 0.5em 3rem -1rem rgba(0,0,0,0.5);
}
.modal-card__img {
	width: 100%;
	height: 140px;
	border-radius: 10px 10px 0 0;
	margin-bottom: 12px;
}


.modal-card__title {
	font-family: 'Montserrat';
	font-weight: 600;
	font-size: 20px;
	padding-left: 10px;
}

.card-ws {
	max-width: 350px;
	min-height: 200px;
	background-color: #fff;
	font-family: 'Open Sans';
	font-size: 18px;
	border: 1px solid #eaeaea;
	border-radius: 10px;
	padding-top: 20px
}

.card-ws__title {
	font-weight: 700
}

.card-ws__description {
	font-weight: 400;
	padding: 0 30px 24px 10px
}

.card-ws__icon {
	width: 50px;
	height: 50px
}

.card-s {
	max-width: 320px;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center
}

.card-s__img {
	max-width: 200px;
	height: 155px;
	margin-bottom: 10px
}

.card-s__title {
	font-weight: 600;
	font-size: 24px;
	font-family: 'Montserrat';
	color: #32373e;
	margin-bottom: 10px
}

.card-s__description {
	color: #5d636b;
	font-family: 'Open-Sans';
	font-weight: 400;
	font-size: 16px;
	text-align: center
}

.toggle__group {
	display: flex;
	justify-content: space-between
}

.toggle__item {
	max-width: 540px;
	width: 100%;
	min-height: 60px;
	display: inline-flex;
	flex-direction: column;
	justify-content: center;
	border-bottom: 1px solid silver
}

.toggle__header {
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	padding-left: 5px
}

.toggle-item__title {
	font-family: 'Montserrat';
	font-weight: 500;
	font-size: 16px
}

.toggle-item__body {
	display: none;
	padding-top: 10px
}

.toggle-show {
	display: block
}

.about-airline {
	display: flex;
	gap: 20px
}

.airline-info {
	max-width: 745px;
	width: 100%;
	min-height: 384px;
	background: url("../../../public/assets/images/airlines.jpg") center/cover no-repeat;
	border-radius: 5px;
	padding-top: 15px;
	color: #fff;
	box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, .5)
}

.airline-info__title {
	font-family: 'Montserrat';
	text-align: center;
	font-weight: 700;
	font-size: 24px;
	margin-bottom: 15px
}

.airline-info__description {
	font-family: 'Open-Sans';
	font-weight: 600;
	font-size: 16px;
	padding-left: 3.35%;
	padding-right: 7.9%
}

.card-rating {
	max-width: 350px;
	width: 100%;
	min-height: 385px;
	background-color: #fff;
	box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
	border-radius: 10px;
	padding: 20px 10px
}

.card-rating__item-icon {
	width: 18px;
	height: 18px
}

.card-rating__item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	min-height: 30px;
	padding: 0 15px;
	margin-bottom: 8px
}

.card-rating__item:last-child {
	margin-bottom: 0
}

.order-value {
	color: #000
}

.card-rating__title {
	display: flex;
	gap: 5px;
	color: #3498db
}

.card-rating__chapter {
	font-size: 24px;
	text-align: center;
	margin-bottom: 20px
}

.card-rating__icon-value {
	display: flex;
	align-items: center;
	gap: 10px
}

@media(max-width:768px) {
	.filter-sidebar {
		display: none
	}

	.card-ws {
		max-width: 450px
	}

	.card-ws__container,
	.small-card-group {
		flex-wrap: wrap;
		flex-direction: column;
		align-items: center
	}

	.card-s {
		max-width: 400px;
		padding-top: 20px;
		border: 1px solid silver;
		border-radius: 10px
	}

	.card-s__description {
		padding: 10px
	}
}

@media(max-width:575px) {
	.modal-card {
		max-width: 300px
	}

	.cards-group {
		justify-content: center;
		gap: 30px
	}

	.about-airline {
		flex-direction: column-reverse
	}

	.card-rating {
		max-width: unset;
		width: 100%
	}
}