*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
::-webkit-scrollbar {
	width: 15px;
}

::-webkit-scrollbar-track {
	background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
	background-color: #afafaf;
	border-radius: 5px;
}



h1,p{
    margin: 0;
}
a{
    color: inherit;
    text-decoration: none;
}
a:hover{
    color: inherit; 
}
a:active{
    color: inherit;
}
button{
    border: none;
}

input{
	text-overflow: ellipsis ;
}

.cursor-pointer{
	cursor: pointer;
}
.rotate-180{
    transform: rotate(180deg);
}

.container {
    max-width: 1140px;
	margin: 0 auto;
	position: relative;
} 
 
.bblr-none{
    border-bottom-left-radius: 0;
}

.fw-500{
    font-weight: 500;
 }





 .outer-circle {
	width: 91.65px;
	height: 86px;
	background-color: #2fa6d9;
	border-radius: 150px;
	display: flex;
	justify-content: center;
	align-items: center;
}



.inner-circle {
	width: 80.55px;
	height: 75.58px;
	background-color: white;
	border-radius: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	gap: 2px;
}

.inner-circle__subtext {
	font-family: "Open-Sans";
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 17px;
}
.inner-circle__value {
	font-family: "Open-Sans";
	font-style: normal;
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
}
.load-wrapper{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap:20px;
}


.loading-container{
	width: 100% !important;
	  
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	gap:20px;
	position: absolute;
	top: 40%;

} 
.loading-text{
font-size: 18px;
}


.overflow-hidden{
	overflow: hidden;
}



/* SECTIONS WITH FIGURE */

  .section-with-figure{
	padding-top: 54px;
  }

.svg-decoration{
    position: absolute;
    left: -7%;
    top: 0px;
    z-index: -1;
}
 .description-figure{
    max-width: 430px;
    width: 100%;
    padding-top:135px;
    padding-left: 10px;
 }
 

.description-figure__title{
    
font-style: normal;
font-weight: 600;
font-size: 40px;
line-height: 60px;
margin-bottom: 10px;
/* identical to box height */


color: #FFFFFF;
}
.description-figure__text{
 
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 33px;
padding-right: 55px;
color: #FFFFFF;
}
.section-with-figure__img-container{
	width: 100%;
 
		display: flex;
		justify-content: flex-end;
 
}
.section-with-figure__img{
    
    max-width: 630px;
    max-height: 420px;
    width: 100%;
}

.section-with-figure__wrapper{
    display: flex;
    justify-content: space-between;
}
 

 

@media (max-width:992px) {
	
    .section-with-figure__img{
        max-width: 350px;
        max-height: 300px;
		padding-top: 90px;
	}
  
  
   
}



@media (max-width:835px){
	.section-with-figure__img{
        max-width: 285px;
	}
}

@media (max-width:768px) {
    .section-with-figure__img-container{
       display: none;  
    }
    .svg-decoration{
        left: 7.7% !important;
		top: 4%!important;
    }
    .description-figure{
        max-width: 560px; 
         padding-left: 140px;
         padding-top: 140px;
    }
	.section-with-figure__wrapper {
		gap: 150px;
	}
	.section-with-figure__card-search{
		display: flex;
		justify-content: center;
	}
}

@media (max-width:576px) {
.svg-decoration{
    display: none;

}
.section-with-figure{
	padding-top: 0;
	margin-bottom: 15px;
} 
.section-with-figure__wrapper {
	gap: 20px;
}
.section-with-figure__container{
padding: 0;
}
.description-figure{
    background: linear-gradient(45deg, #FF0066 0%, #FBA81F 100%);
    padding: 0 10px 20px 10px;
     
}
.description-figure__title{
    font-size: 28px;
text-align: center;
}
.description-figure__text{
    font-size: 18px;
	padding: 4px;
}
}


 



