

.statistic-card{
    width: 315px;
    min-height: 165px;
    background-color: #FFFFFF;
    border: 1px solid #E1E1E1;
    border-radius: 10px;
} 
.statistic-card__header{
    min-height: 47px;
    border-bottom: 1px solid #DFDBDB;;
    display: flex;
    justify-content: center;
    align-items: center;
}

.statistic-card__left-title{
    font-family: 'Open-Sans';
 
font-weight: 600;
font-size: 16px;
line-height: 24px;
/* can remove padding its just for last */
  padding-bottom: 5px;

color: #413D3D;
}
.statistic-card__left-value{
    font-size: 16px;
    color: #413D3D;
}
.statistic-card__body{
    min-height: inherit;
    display: flex;
}


.statistic-card__left-col{
    width: 185px;
    padding-top: 12px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
 
} 


.statistic-card__right-col{
    width: 125px;
    border-left: 1px solid #DFDBDB;
     
}

.statistic-card__circle-bar{
    margin-bottom: 14px;
}

.statistic-card__right-col{
    padding-top: 14px;
    padding-left: 10px;
}


.column-item-title,  .column-item-value{
    font-family: 'Poppins';
    font-style: normal;

}

.column-item-title{
 
font-weight: 600;
font-size: 14px;
line-height: 21px;

color: #333333;
}
.column-item-value{
 
font-weight: 400;
font-size: 20px;
line-height: 30px;

color: #413D3D;
}

.statistic-card__left-item-title{
    margin-bottom: 10px;
}
.statistic-card__right-items{
    display: flex;
    flex-direction: column;
}
.statistic-card__right-items:first-child{
    margin-bottom: 20px;
}




.statistic-card__img{
    display: block;
    width: 150px;
    height: 95px;
    object-fit: fill;
    border-radius: 10px;
    margin-bottom: 10px;
 }
 .circle-item{
    width: 72px;
    height: 72px;
    background-color: #6ECCE0;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
 }
 .circle-item__value{
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color:white;
 }
.statistic-card__right-col-gap{
    gap: 9px;
}
p#current-date{
    font-family: 'Poppins';
    font-weight: 500;
    font-size: 12px;
    line-height: 18px; 
    color: #B8B8B8;
} 
