



::-webkit-scrollbar {
    width: 15px;
    
  }
  
  ::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, #2193b0, #6dd5ed);
 
    border: 0px none #ffffff;
  }
  
  ::-webkit-scrollbar-track {
    background: white;
    border: 0px none #ffffff;
    border-radius: 0px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #27aeef;
  }


#aircraft-live-map {
	width: 100%;
	min-height: 600px;
	position: relative; 
}

.aircraft-map-section{
    position: relative; 
    margin-bottom: 50px;
	 
}
.aircraft-map__container{
	border-radius: 10px;
}

.my-div-icon {
	width: 40px;
	height: 40px;
	background-color: red;
}
.aircraft-map {
	position: relative;
	z-index: 2;
}
.aircraft-card {
	position: absolute;
	top: 2%;
	right: 0.7894%;
	z-index: 3;
	transition: all 0.4s ease-in;
}
.input-item {
	max-width: 210px;
	min-height: 50px;
	width: 100%; 
	border-radius: 5px;
	text-align: center;
}
 .search-card__error-message{
    text-align: center;
    padding: 5px 5px 10px 5px ;
 
 }
 
.aircraft-page .description-figure__text{
    padding-right: 70px;
} 
/*  start view-description */

.aircraft-page .view-description:first-of-type{
    margin-bottom: 30px;
}
.aircraft-page__title{
    text-align: center;
    margin-bottom: 20px;
    font-weight: 600;
}
.view-description{

    background-color: #4D76C6;
}
.view-description__container{
 
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 400px;
    color: white;
    margin: 0 auto;
}

.view-description__description{
    max-width: 550px;
    width: 100%; 
    padding: 20px;

}
.view-description__title{

    font-family: 'Open-Sans';
    
    font-weight: 600;
    font-size: 38px;
    margin-bottom: 15px;

}
.view-description__text{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 29px;
   


}

.view-description__img{
    max-width: 450px;
    max-height: 310px;
    border: 5px solid #FFFFFF;
    border-radius: 15px;

}
.section-with-figure__svg-container{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.aircraft-page .svg-decoration-search-panel{
    left:-10%;
    top:5.5%;
}


.aircraft-page .description-figure{
    padding-top: 85px;
}

.section-with-figure__svg{

    max-width: 500px;
    max-height: 500px;
    width: 100%;
}


.aircraft-page .wrapper-search{
    gap:10px;
}

.search-card__btn{
    width:100%;
    height:50px;
    background-color: orange;
    color:white;
    border-radius: 4px;
}


.search-aircraft{
    margin-bottom: 50px;
}




@media (max-width:992px) {
    .aircraft-page .svg-decoration-search-panel{
        left:-11%;
    }
}

@media(max-width:868px){
   .aircraft-page .search-card input{
    min-height: 40px;
   }
    .aircraft-page .section-with-figure__svg{

        max-width: 300px;

    }
    
    .aircraft-page   .section-with-figure__svg-container{
        width:50%;
        padding-top: 70px;
    }

    .aircraft-page .svg-decoration-search-panel{
        left:-14%;
         
    }
    .aircraft-page .description-figure__text{
        line-height: 30px;
        padding-right: 40px;
    } 
     
}





@media (max-width:768px) {

.aircraft-map-section{
    padding: 0 25px;
    
}

#aircraft-live-map{
    border-radius: 10px;
}
    
    .aircraft-page .description-figure {
padding-top: 115px;
    }
    .view-description__container{
        flex-direction: column ;
    }
    .view-description__description {
        max-width: 90%;
        padding: 0;
        margin-bottom: 15px;
    }
    .view-description__img{
        max-width: 90%;
        max-height: unset;
    }
    .aircraft-page   .section-with-figure__svg-container{
        display: none;
    }
    .aircraft-page .search-card input{
        min-height: 50px;
       }
      
    .aircraft-card{
right: 5.7894%;
    }
}


@media (max-width:576px){
    .aircraft-page .description-figure {
        padding-top: inherit;
    }
    
}


@media (max-width:435px) {

.view-description__title{
    font-size: 28px;
}
.aircraft-card-wrapper{
position: fixed;
background-color: white;
left: 0;
top: 0;
z-index: 100;
width:100%;
height:100vh;
}
.aircraft-card {
    right:0;
    left:0;
    
    margin : 0 auto;
}
.aircraft-page .search-card{
    padding:  10px;
}
 

}






/* end view-description */




.description-section {
    color:#fff; 
    overflow:hidden;
    min-height:530px
    }
    
    .description-section__decoration {
    position:absolute;
    right:-5%;
    bottom:-10%
    }
    
    .description-section__item {
    display:flex;
    gap:30px;
    padding:64px 7px 24px 21px;
    background:linear-gradient(101.56deg,#fa8b29 5.15%,#ff1414 159.08%);
    background-blend-mode:exclusion;
    border:1px solid #dedede;
    box-shadow:0 1px 3px 1px rgba(0,0,0,.29);
    border-radius:10px;
    position:relative
    }
    
    .description-section__container {
    display:flex;
    align-items:flex-start
    }
    
    .description-section__title {
    font-family:'Montserrat';
    font-style:normal;
    font-weight:700;
    font-size:32px;
    line-height:39px;
    margin-bottom:20px
    }
    
    .description-section__text {
    font-family:'Poppins';
    font-style:normal;
    font-weight:400;
    font-size:22px;
    line-height:33px;
    letter-spacing:.01em
    }
    
    .description-section__img-wrapper {
    border-radius:15px;
    max-width:450px;
    max-height:310px;
    padding-top:45px;
    width:100%
    }
    
    .description-section__img {
    max-width:inherit;
    max-height:inherit;
    border-radius:inherit;
    border:2px solid #16E7E7
    }
    
    .description-section__container {
    display:flex
    }
    
    @media (max-width:992px) {
    .description-section__title {
    text-align:center
    }
    
    .description-section__item {
    flex-direction:column;
    align-items:center
    }
    
    .description-section__img-wrapper {
    padding-top:0
    }
    }
    
    @media (max-width:576px) {
    .description-section__item {
    padding:15px
    }
    
    .description-section__img-wrapper {
    max-width:300px
    }
    }
    
    @media (max-width:350px) {
    .description-section__item {
    padding:15px
    }
    
    .description-section__img-wrapper {
    max-width:250px
    }
    }

    /* end description section */