* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html {
	font-family: "Open Sans", sans-serif;
}
input {
	border: none;
	outline: none;
}
.bold {
	font-weight: bold;
}
.container {
	max-width: 1140px;
	margin: 0 auto;
}
.top_section__container {
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
}
.top_section {
	min-height: 700px;
	background-color: white;
}

   
 
.standart-autocomplete input{

width: 380px;

min-height: 60px; 
}
.form-select {
	max-width: 350px;
}

.wrapper-search {
	position: relative;
}

/* card section */
.section-with-figure__container{
	min-height: 495px;
  }
.section-with-figure__card-search{
	width:100%;
	padding-top: 50px;
}
.section-with-figure__card-search{
	padding-left: 30px;
}
.card-info__top {
	min-height: 130px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: url("../../../public/assets/icons/airport-with-airplane.svg");
	background-position: 10px center;
	background-repeat: no-repeat;
	background-size: 65px 65px;
}
.card {
	max-width: 1050px;
	background: #ffffff;
	border: 1px solid #e1e1e1;
	box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
	border-radius: 15px;
}
.card-info {
	margin-bottom: 40px;
	padding-left: 10px;
	padding-right: 10px;
}
.card-info__header {
	font-weight: 600;
	font-size: 36px;
	line-height: 49px;
	text-align: center;
}
.card-info__content {
	display: flex;
	justify-content: center;
	align-items: center;
}

.card-info__left-side {
	max-width: 400px;
	width: 100%;
	padding-right: 20px;
}
.card-info__right-side {
	width: 100%;
	min-height: 550px;
}

.airport-map-location {
	max-width: 640px;
	width: 100%;
	min-height: 530px;
	border-radius: 10px;
}
.benefits-list{
	margin-bottom: 40px;
}
.benefits-list__header {
	text-align: center;
	font-weight: 700;
	font-size: 32px;
	margin-bottom: 30px;
}
.benifits-item-list {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	gap:10px;
}
.benefits-item {
	width: 345px;
	min-height: 300px;
	padding-left: 25px;
	padding-right: 15px;
	color: white;
	font-family: 'Open Sans';
	padding-top: 30px;
	border-radius: 15px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.benefits-item__title{
	font-weight: 700;
font-size: 20px;
line-height: 27px;
}

.benefits-item__description{

	font-weight: 600;
font-size: 20px;
line-height: 27px;

color: #FFFFFF;
}

.benefits-item:nth-child(1) {
	background-color: #E74C3C;
}
.benefits-item:nth-child(2) {
	background-color: #16A085;
}
.benefits-item:nth-child(3) {
	background-color: #9B59B6;
}

.benefits-item__top {
	display: flex;
	margin-bottom: 20px;
}
.benefits-item__top img {
	width: 30px;
	height: 33px;
}

.benefits-item span {
	display: block;
	padding-left: 10px;
	font-weight: 700;
	font-size: 24px;
}
.benefits-item p {
	font-weight: 400;
	font-size: 20px;
}
svg.svg-decoration-search-panel {
	top: 7%;
	left:-8%;
}
/* media queries */

@media (max-width: 992px) {
	.top_section__description {
		font-size: 28px;
	}

	.benefits-item {
		max-width: 90%;
		min-height: 230px;
		width: 100%;
		margin: 0 auto;
	}
	.benefits-item:nth-child(-n + 2) {
		margin-bottom: 20px;
	}
	.card-info__header {
		font-size: 26px;
	}

	.section-with-figure__card-search .search-card__title{
		
		font-size: 18px;
	}
	.section-with-figure__card-search .search-card__sub-title{
		padding:0 5px;
		text-align: center;
		font-size: 16px;
	}
	.section-with-figure__card-search input{
		width:210px;
	}
	
}
@media (max-width:850px) {
	svg.svg-decoration-search-panel {
		top: 7%;
		left:-15%;
	}
}
@media (max-width: 768px) {
	.top_section {
		min-height: 500px;
	}
	.top_section__title {
		font-size: 48px;
	}
	.card-info__top {
		background: none;
	}
	 
	.section-with-figure__wrapper {
		flex-direction: column;
	}
	.svg.svg-decoration-search-panel{
		padding-left: 0;
	}
	.section-with-figure__card-search  {
		padding: 0;
		margin-bottom: 20px;
	}
}
@media (max-width: 654px) {
	
	.top_section__img {
		display: none;
	}
	.top_section__left-wrapper {
		width: 100%;
		min-height: 500px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
	}
	.top_section__description {
		padding-right: 50px;
	}

	
	.card-info__content {
		flex-direction: column;
	}
	.card-info__left-side {
		max-width: 100%;
	}
}

@media (max-width:576px) {
	.section-with-figure{
		padding: 0;
	}
	 .section-with-figure__container{
		min-height: unset;
	  }
	   .section-with-figure__card-search{
		padding: 10px;
	   }
		 
}

@media (max-width: 480px) {
	
	.standart-autocomplete input{
		width: 250px;
		min-height: 50px;
	}
	.top_section__description {
		font-size: 24px;
		line-height: 48px;
	}
	.benefits-item{
		padding-bottom: 20px;
	}
	.benefits-list__header {
		font-size: 40px;
	}
	.description-figure__title{
		line-height: 40px;
	}
}
@media (max-width: 370px) {
	.card-info__right-side{
		min-height: auto;
		padding-bottom: 20px;
	}
	.card-info__header {
		padding-top: 20px;
		font-size: 24px;
	}
	.benefits-item {
		max-width: 100%;
	}
	
	.airport-map-location {
		min-height: 300px;
	}
}
 




 

