.full-list{
    padding: 40px 0;
}
.full-list__item{ 
    width: 25%;
    padding-top: 20px;
    color:cornflowerblue;
    font-family: 'Poppins';
    font-weight: 600;
   
}
.full-list__title{
margin-bottom: 45px;
}
@media (max-width:957px) {
    .full-list__item{
        width: 33.33%;
    }

}
@media (max-width:768px){
    .full-list__item{
        width: 50%;
         
        padding-right: 10px;
    }
}
 