.list-items {
	max-width: 1140px;
	width: 100%;
	display: flex;
	flex-wrap: wrap;  
	gap: 1%;
	padding-top: 30px;
	padding-bottom: 30px;
	padding-left: 10px;
		padding-right: 10px;
	border: 1px solid silver;
	background-color: white;
	border-radius: 10px;
	justify-content: flex-start;
	 
}
.list-item{
	display:flex; 
	align-items:center;
	flex-basis:24%;  
	min-height:40px;
	padding-bottom: 30px; 
  }
  .list-item__title{
	padding-left: 10px;
	padding-right: 10px;
  }
 
.list-item__img {
	width: 40px;
	height: 40px;
}

@media (max-width:678px) {
	 
	.list-item{
		flex-basis: 49%;
	}

}


@media(max-width:435px) {


	.list-items{
		gap: 20px;
	}

	.list-item{ 
		padding:0  5px 0 5px;

		flex-basis: 100%;
		gap: 10px;
	}
}