 
.search-card {
  
	max-width: 585px;
	min-height: 300px;
	width: 100%;
	background-color: black;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 10px;
	padding-top: 30px;
	 
	padding-bottom: 40px;
	background-color: #3498db;
}

.search-card__icon {
	width: 55px;
	min-height: 55px;
	background: url("../../../public/assets/icons/airport.svg") no-repeat;
   
	background-size: 100%;
	margin-bottom: 12px;
}
 

.main_section__search-card-title {
	padding-top: 15px;
	margin-bottom: 12px;
}
.main_section__search-card-sub-title {
	margin-bottom: 22px;
	text-align: center;
}

.search-card__title {
	font-weight: 700;
    font-size: 28px;
    line-height: 38px;
	text-align: center;
   
}
.search-card__title_mb{
    margin-bottom: 12px;
}


.search-card__sub-title {
	font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    margin-bottom: 20px;
}
.search-card__sub-title_mb{

margin-bottom: 20px;
}

.search-card__search-panel {
	max-width: 500px;
	width: 100%;
	height: 70px;
	margin-bottom: 10px;
	font-family: "Moul";
	font-style: normal;
	font-weight: 400;
	font-size: 24px;
	text-align: center;
	border-radius: 5px;
}

@media (max-width:654px) {
    .search-card {
		padding-bottom: 30px;
		min-height: 300px;
	}
    .search-card__title {
		font-size: 38px;
		margin-bottom: 15px;
	}
	.search-card__sub-title,
	.search-card__search-panel {
		font-size: 20px;
	}
	.search-card__search-panel {
		max-width: 400px;
		height: 60px;
	}
}

@media (max-width:480px) {
    .search-card__title {
		font-size: 28px;
	}
    .search-card__sub-title{
        text-align: center;
        padding-left: 10px;
        padding-right: 10px;
    }
	.search-card__search-panel {
		max-width: 90%;
       
	}
}


@media (max-width:370px){
    .search-card__title {
		font-size: 23px;
	}
	.search-card__sub-title,
	.search-card__search-panel {
		font-size: 18px;
	}
}