
.flight-tickets{
	padding-top: 40px;
}



.flight-tickets__container{
	width: 100%;
	display: flex;
	gap: 20px;
	padding-bottom: 20px;
}
.flight-tickets__tickets{
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: 40px;
}

.toolbar{
	display: flex;
	justify-content: flex-end;
	margin-bottom: 20px;
}


.filter-btn{
	display: none;
}

.filter-wrapper{
	max-width: 300px;
	width:100%;
 	background-color: white;
  
}


.select-sort{
	 max-width: 270px;
	 
}

 
.information-item{
	width:100%;
	min-height: 80vh;
	display: flex;
	flex-direction: column;
	gap: 40px;
}
.information-text  {
font-size: 28px;
font-family: 'Open-Sans';
font-weight: 600; 
} 


 .information-img{
	max-width: 100%;
	border-radius: 30px;
	margin-bottom: 30px;
 }
.error-img{
	max-width:500px;
	width:100%;

 }

@media (max-width:768px) {
	 
	.filter-invisible{
		display: none;
	}
	 
	.flight-tickets__container{
		display: block;
	}
	
	.filter-visible{
		width: 100%;
		display: block;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 100;
		padding: 5px;
	}
	 
	.toolbar{
		 
		gap: 20px;
	}

	.filter-wrapper{
		max-width: unset;
		  
	}

	.filter-btn{
		display: flex;
		max-width: 55px;
		height: 45px;
		width:100%;
		 
		align-items: center;
		justify-content: center;
		background-color: #52b064;
		color:white;
		border-radius: 5px;
	}
	 
}
 @media (max-width:576px) {
	.information-text{
		font-size: 18px;
	}
 }