 
.inner-space{
    padding: 5px;
}
.statistic-section{
    margin-bottom: 20px;
    padding: 0 10px;
}
.statistic-section .statistic-section__container{
    border:1px solid #d1d1d1;
    border-radius: 20px;
    padding: 25px 20px;
  
}
.statistic-section__cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 20px;
    margin-bottom: 40px;

}
.statistic-section__title{
    font-family: 'Montserrat';
    font-weight: 700;
     text-align: center;
    margin-bottom: 20px;
   
}

@media (max-width:992px) {
    .statistic-section__cards{

        justify-content: center;
        gap:20px
    }
    .statistic-card{
        max-width: 355px;
        width: 100%;
    }
}


@media (max-width:768px) {
    .statistic-section__cards{

        flex-direction: column;
        align-items: center;
        gap:40px;
    }
     
     
} 