label{
	margin: 0;
}
.filter-sidebar{
	/* max-width: 275px; */
	width: 100%;
 
	display: flex;
	justify-content: center;
	padding: 10px;
	border:1px solid silver;
}

.filter-sidebar__container{
	/* max-width: 255px; */
	width: 100%;
	background-color: white;
	padding-top: 15px;
	padding-bottom: 15px;
	padding-left : 10px; 
	padding-right : 10px; 
	font-family: 'Open Sans';
	overflow-y: scroll;
	
}
.filter-sidebar__header{
min-height: 50px;
display: flex;
align-items: center;
justify-content: space-between;
padding-bottom: 10px;
margin-bottom: 10px;
border-bottom: 1px solid silver;
font-family: 'Montserrat';
font-weight: 500;
}
.filter-sidebar__title{
	display: flex;
	align-items: center;
	gap: 10px;
	font-size: 20px;
}
.filter-sidebar__title i {
	 display: none;
	font-size: 22px;
}
.filter__title{
	display: flex;
 
font-family: 'Montserrat';
	font-style: normal;
	font-weight: 600;
 margin-bottom: 5px;
}
.filter__item{
display: flex;
align-items: center;
 

}
.filter-reset-btn{
   
border-radius: 5px;
 max-height: 40px;
}


.filter__group{ 
	border-bottom: 1px solid silver;
	margin-bottom: 10px;
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding-bottom: 10px;
}
.filter__list-values{
	max-height: 285px;
	overflow-y: scroll;
	display: flex;
		flex-direction: column;
		gap: 10px;
		padding-bottom: 10px; 
}

 


.filter__list-values::-webkit-scrollbar {
	width: 8px;

}

.filter__list-values::-webkit-scrollbar-track {
	background-color: #f1f1f1;
		border-radius: 5px;
}

.filter__list-values::-webkit-scrollbar-thumb {
	background-color: #8d8c8c;
	border-radius: 5px;
}

.filter__list-values::-webkit-scrollbar-thumb:hover {
	background-color: #747474;
}
 



.filter-item__title{
	font-family: 'Open Sans';
 
		font-weight: 400;
		font-size: 15px;
		padding-left: 5px;
}
.filter__sub-value{
	font-size: 13px;

}
button.filter__search-btn,
button.filter__search-btn:hover{
	width: 100%;
	border-radius: 3px;
	background-color: #27AE60;
	color: white;
	min-height: 45px;
}  


@media (max-width:768px) {
 
	.filter-sidebar__title{
		cursor: pointer;
	}

	.filter-sidebar__title i {
		display: inline;
	    
   }

	.filter-sidebar__container{
		height: 100vh;
		overflow-y: scroll;
		padding-bottom: 30px;
	}
	.filter-sidebar{
		border:none;
		max-width: unset;
		display: block;
		padding: 0;
		 
	}
	 
	.filter-sidebar__container{
		max-width: unset;
	}
}