 

  
 

.card-flight {
	max-width: 360px;
	width: 100%;
	background-color: white;
	border-radius: 10px;
	padding-left: 10px;
	padding-right: 10px;
	padding-top: 20px; 
	max-height: 560px;
	overflow-y: scroll;
	padding-bottom: 20px; 

	font-family: "Open-Sans";
}

.card-flight::-webkit-scrollbar { 
	width: 10px;
  }
  
   


.container {
	max-width: 340px;
	
}
span.close-card{
position: absolute;
top: 10px;
right: 13px;
font-size: 24px;
cursor: pointer;
z-index: 10;
font-weight: bolder;
color:#f12e41 ;

}
.airline-info__card-container{
background-color: white;
padding: 0px;
}
.airline-info__number {
	margin-bottom: 5px;
}

.airline-info__flight-number {
	font-weight: 100;
	font-size: 18px;
}

.airline-info__name {
	display: block;
	margin-bottom: 10px;
	font-size: 18px;
}

.aircraft__img {
	display: block;
	max-width: 100%;
	width: 100%;
	max-height: 200px;
	border-radius: 10px;
	margin-bottom: 10px;
}

.way-info__icon,
.aircraft-info__icon {
	max-width: 20px;
}

/* .aircraft-info__icon {
	background-image: url("../../assets/icons/plane-icon.svg");
} */

.way-info {
	border: 1px solid silver;
	border-radius: 10px;
	margin-bottom: 15px;
	position: relative;
}
.way-info__container {
	border-radius: 10px;
	min-height: 160px;
}

.way-info__container {
	display: flex;
	flex-direction: row;
}
.way-info__left-side {
	border-right: 1px solid #e6e6e6;
}
.way-info__left-side,
.way-info__right-side {
	width: 50%;
}

.way-info__left-title,
.way-info__right-title {
	font-style: normal;
	font-weight: 600;
	font-size: 20px;
	line-height: 22px;
}

.way-info__top-left {
	display: flex;
	align-items: center;
	padding-left: 15px;
	min-height: 44px;
	background-image: url("/public/assets/icons/take_off.svg");
	background-size: 40px 40px;
	background-repeat: no-repeat;
	background-position: right 20px center;
	font-size: 18px;

	margin-bottom: 15px;
}

.way-info__top-right {
	display: flex;
	align-items: center;
	padding-left: 15px;
	min-height: 44px;
	background-image: url("../../../public/assets/icons/arrive.svg");
	background-size: 40px 40px;
	background-repeat: no-repeat;
	background-position: right 20px center;
	font-size: 18px;
	margin-bottom: 15px;

	border-top-right-radius: 10px;
}

.way-info__left-departure,
.way-info__right-arrive {
	font-style: normal;
	font-weight: 700;
	font-size: 36px;
	line-height: 49px;
	text-align: center;
	margin-bottom: 15px;
}

.way-info__left-departure-city,
.way-info__right-arrive-city {
	display: block;
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 22px;

	text-align: center;
}

.circle-icon {
	position: absolute;
	width: 58px;
	height: 63px;
	background-color: white;
	top: 49px;
	left: 41.170588%;
	background-image: url("../../../public/assets/icons/circle-arrive.svg");
	background-position: center;
	background-size: 100%;
	z-index: 2;
}

.flight-schedule {
	max-width: 340px;
	min-height: 118px;
	display: flex;
	flex-wrap: wrap;
	background: #ffffff;
	box-shadow: 1px 3px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
	margin-bottom: 15px;
}

.flight-schedule__part {
	width: 50%;
}

.flight-schedule__part:first-child {
	border-right: 1px solid #f2f2f2;
}

.flight-schedule__timetable {
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid #ededed;
	padding-left: 10px;
	padding-right: 7px;
	min-height: 40px;
	align-items: center;
}

.flight-schedule__timetable span:first-child {
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 19px;
}
.time {
	font-style: normal;
	font-weight: 700;
	font-size: 17px;
	line-height: 21px;
}

.progress-icon {
	width: 27px;
	height: 27px;
	position: absolute;
	background-image: url("../../../public/assets/icons/aircraft-progress.svg");
	background-size: 27px 27px;
	z-index: 2;
	left: 8px;
}

.progress-flight {
	padding-left: 10px;
	padding-right: 10px;
	width: 100%;
	display: flex;
	align-items: center;
	position: relative;
	min-height: 40px;
}
.progress-line {
	width: 100%;
	background: #c7c5c5;
	border-radius: 10px;
	transform: matrix(1, 0, 0, 1, 0, 0);
	height: 5px;
}
.progress-flight__progress {
	background: rgb(84, 173, 0);
	width: 0;
	height: inherit;
}
.aircraft-info__top {
	display: flex;
}

.aircraft-info__chapter {
	 
	font-weight: 700;
	font-size: 16px;
	line-height: 19px;
}

.aircraft-info__item {
	min-height: 70px;
	 
	width: 50%;
	padding:10px 8px 10px 10px;
}
.aircraft-info__item:first-child {
	border-right: 1px solid #000000;
}

.aircraft-info__aircraft,
.aircraft-info__registration {
	padding-top: 13px;
	display: flex;
	align-items: center;
	gap: 10px;
}

.aircraft-info {
	border-radius: 5px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	margin-bottom: 15px;
}

.aircraft-info__bottom {
	border-top: 1px solid black;
}

.aircraft-info__aircraft span {
	 
	font-weight: 400;
	font-size: 15px;
	line-height: 18px;
}

.aircraft-info__altitude {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	min-height: 70px;
	background-image: url("../../../public/assets/icons/altitude.svg");
	background-size: 50px 50px;
	background-repeat: no-repeat;
	background-position: 5px center;

	  
	font-weight: 700;
	font-size: 16px;
	line-height: 22px;
}
.aircraft-info__altitude h2 {
	font-size: 18px;
	margin-bottom: 10px;
	font-weight: bold;
}

.aircraft-indicators {
	display: flex;
	gap: 15px;
	margin-bottom: 15px;
}

.aircraft-indicators__item:first-child {
	display: flex;
	align-items: center;
	gap: 25px;
	padding-left: 10px;
	padding-right: 10px;
	width: 100%;
	max-width: 205px;
	min-height: 105px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
}

.aircraft-indicators__item:nth-child(2) {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: 15px;
	max-width: 120px;
	width: 100%;
	min-height: 105px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
}
.aircraft-indicators__value {
	padding-top: 10px;
}

.aircraft-indicators__title {
	
	

	
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
} 
.value {
	 
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
}

.satellite-info {
	display: flex;
	flex-direction: column;
	min-height: 128px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
}

.satellite-info__top,
.satellite-info__bottom {
	display: flex;
	width: 100%;
	min-height: 64px;
}

.satellite-info__top {
	border-bottom: 1px solid black;
}

.satellite-info__top-icon {
	background-image: url("../../../public/assets/icons/long.svg");
}

.satellite-info__top-icon,
.satellite-info__bottom-icon {
	max-width: 45px;
	width: 100%;
	border-right: 1px solid black;
	background-size: 30px 30px;
	background-repeat: no-repeat;
	background-position: center;
}

.satellite-info__bottom-icon {
	background-image: url("../../../public/assets/icons/radar.svg");
}

.satellite-info__data {
	display: flex;
	width: 100%;
	justify-content: space-between;
	padding-left: 10px;
	padding-right: 10px;
	padding-top: 5px;
}
.satellite-info__data-col {
	display: flex;
	flex-direction: column;
}

.satellite-info__chapter {
	display: block;
	margin-bottom: 5px;
	padding-right: 5px;
	 
	font-weight: 400;
	font-size: 17px;
	line-height: 22px;
}

.satellite-info__bottom .satellite-info__data-col {
	padding-right: 7px;
}

.satellite-info__value {

	
	font-weight: 600;
	font-size: 17px;
	line-height: 20px;
}



.card-flight-loading{
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: silver;
	opacity: 0.7;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 20;
	height: 100%;
}

@media (max-width: 445px) {
.way-info__left-departure,
.way-info__right-arrive{
	font-size:24px;
}
.circle-icon{
width: 67px;
left: 38.10058823529412%;

}
.way-info__right-arrive-city{
	padding-left: 2%;
}
.way-info__left-arrive-city{
padding-right: 2%;
}
}


@media (max-width: 340px) {
	.flight-schedule__timetable span:first-child {
		font-size: 14px;
	}
}

@media (max-width: 310px) {
	.circle-icon {
		width:57px;

		left: 39.500588%;
	}
	.way-info__left-departure, .way-info__right-arrive {
		font-size:18px ;
	}
	.aircraft-info__altitude h2, .aircraft-indicators__title {
		font-size: 14px;
	}
	.aircraft-indicators__item:first-child{
gap:12px;
	}
}
