.item input,
button,
input {
	border: none
}

* {
	margin: 0;
	padding: 0
}
 
.search-panel {
	background-color: #0ff
}

.list-group {
	max-height: 250px
}

.search-panel__search-form,
.search-passengers {
	position: relative
}

.autocomplete {
	position: absolute;
	top: 70px;
	background-color: #faebd7;
	list-style: none;
	width: 100%
}

.autocomplete__item {
	color: #000
}

.list-group-item:hover {
	background-color: #eee;
	transition: .4s cubic-bezier(.165, .84, .44, 1);
	cursor: pointer
}

.search-section {
	min-height: 570px;
	background-repeat: no-repeat;
	background-size: cover
}

.search-section__search-container {
	width: 100%;
	min-height: 200px;
	padding-top: 40px;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	align-items: flex-end;
	position: relative;
}

.search-section__title {
	font-family: 'Open-Sans';
	margin: 0 0 35px;
	padding-top: 40px;
	font-size: 28px;
	color: #fff;
	font-weight: 700;
}

.item input,
.title {
	font-family: "Open Sans";
	font-style: normal;
	font-weight: 700
}

.item {
	max-width: 225px;
	width: 100%
}

.small-item {
	max-width: 140px
}

.item input {
	width: 100%;
	min-height: 75px;
	background: 12px center no-repeat #fff;
	border-right: 1px solid #c9c9c9;
	border-radius: 0;
	padding-left: 35px;
	color: #383669;
	font-size: 16px
}

.search-section__item {
	border: none;
	position: relative
}

.error-message,
.search-panel__item,
.search-panel__item input,
.search-passengers input:first-child,
.search-section__item input {
	border-radius: inherit
}

.search-section__item:first-child {
	border-radius: 10px 0 0 10px
}

.search-section__item:nth-last-child(-n+2) {
	margin-right: auto;
	border-radius: 0 10px 10px 0
}

.search-section__content {
	width: 100%;
	min-height: 150px;
	background-color: #7fffd4
}

.title {
	font-size: 36px;
	line-height: 49px
}

.search-section__search-btn {
	max-width: 145px;
	width: 100%
}

.search-section__search-btn button {
	min-height: 75px;
	width: 100%;
	background: linear-gradient(261.82deg, #FF932F 111.99%, rgba(255, 177, 27, 0) 397.13%);
	border-radius: 10px;
	color:white;
	font-family: 'Open-Sans';
	font-weight: 700;
}

.search-panel__item {
	width: 100%
}

.i-invalid input {
	box-shadow: 0 0 0 2px #f8624e;
	position: relative;
	z-index: 1;
}

.error-message {
	width: 100%;
	color: #fff;
	background-color: tomato;
	padding-left: 10px;
	font-size: 14px;
	border-bottom-left-radius: unset;
	border-bottom-right-radius: unset;
	position: absolute;
	top: 0;
	z-index: 2;
}
 



.item:first-of-type input {
	background-image: url("../../../public/assets/icons/departureAircraft.svg");
	  
}
.item:nth-of-type(2) input{
	background-image: url("../../../public/assets/icons/takeOff.svg");
	background-position: 5px center;
}
.item:nth-child(3) input, .item:nth-child(4) input{
	background-image: url("../../../public/assets/icons/calendar.svg");
} 
.item:nth-child(5) input{
	background-image: url("../../../public/assets/icons/passenger.svg");
}
.item:nth-child(-n+5) input {
	background-position: 5px center;
}



@media (max-width:1140px) {
	.autocomplete {
		max-width: inherit
	} 

	.search-section__item:first-child {
		border-radius: 10px 10px 0 0
	}

	.search-section__item:nth-child(3) {
		border-radius: 0 0 0 10px
	}

	.search-section__item:nth-child(2) {
		border-radius: 0
	}

	.search-section__item input {
		border-right: 1px solid silver
	}

	.search-section__item:nth-child(-n+2) {
		max-width: 50%;
		
		border-bottom: 1px solid silver
	}
	.search-section__item:first-of-type{
		 border-top-right-radius: unset; 
	}
	.search-section__item:nth-child(2){
		border-top-right-radius: 10px; 
   }

	.small-item {
		max-width: 25%;
		margin-right: 0
	}

	.search-section__item:last-of-type {
		max-width: 50%;
		border-radius: 10px
	}

	.search-section__item:nth-last-child(-n+2) {
		max-width: 50%
	}

	.search-section__item:nth-last-child(-n+2) input {
		border-radius: 0 0 10px
	}

	.search-section__search-btn {
		max-width: 100%;
		padding-top: 10px;
		display: flex;
		justify-content: flex-end
	}

	.search-section__search-btn button {
		border-radius: 5px;
		max-width: 30%
	}
}

.sub-menu {
	min-width: 320px;
	max-width: 320px;
	border: 1px solid silver;
	margin: 0 auto;
	padding: 15px 0
}

.sub-title {
	font-size: 13px;
	color: #4d5864
}

.sub-menu__item {
	margin-bottom: 10px
}

.sub-menu__item:last-child {
	margin-bottom: 0
}

.passenger-counts {
	padding-bottom: 10px;
	border-bottom: 1px solid silver
}

.passanger-class {
	padding-top: 10px;
	margin-bottom: 15px
}

.passanger-class__title {
	display: block;
	margin-bottom: 15px
}

.passanger-class__btns-container {
	display: flex;
	gap: 20px
}

.search-item__dialog {
	position: absolute;
	right: 0;
	z-index: 15
}

@media (max-width:740px) {

	.i-date,
	.small-item {
		border-bottom: 1px solid silver
	}

	.search-section__title {
		text-align: center
	}

	.list-group {
		max-height: 330px
	}

	.small-item {
		max-width: 50%
	}

	form.form-container div.search-section__item:first-child {
		border-radius: 10px
	}

	.search-section__item:first-child input {
		border-radius: 10px 10px 0 0
	}

	.search-section__item:nth-child(-n+2),
	.search-section__item:nth-last-child(-n+2),
	.search-section__search-btn button,
	div.search-section__item input {
		max-width: 100%
	}

	 

	.search-section__item:nth-last-child(-n+2) input {
		border-radius: 0 0 10px 10px;
 
	} 

	 
	
	.search-section__item:nth-child(2) ,
	.search-section__item:nth-child(3) input {
		border-radius: 0;
	}
	 
	.search-item__dialog {
		width: 100%;
	}
}

@media (max-width:480px) {
	.autocomplete {
		max-width: 90%
	}
}