.airline-page{
	 
	display: flex; 
	justify-content: center;
	flex-direction: column;
}


.airline-page .airline-fleet::-webkit-scrollbar-thumb {
    background-color: #27aeef;  
  }




.page-section{
	margin-bottom: 40px; 
}


.airline-page__card-section{
	min-height: 200px;
	padding-top: 50px;
	background-color: white;
	border-radius: 10px;
}


.card-section__title{
	font-family: 'Open-Sans';
	font-weight: 500;
	margin-bottom: 30px;
}
.airline-contact {
	border-radius: 10px;
	background-color: white;
}
/* 
airline contact start styles */



.airline-contact__container{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	gap: 10px;
	border: 1px solid silver;
	padding : 25px 13px ; 
	
}
.airline-contact__item{
	display: flex;
	align-items: center;
	gap: 10px;
}
.airline-contact__item-info{
	display: flex;
	flex-direction: column;
}

.airline-contact__icon{
	width:30px;
	height:30px;
}

.airline-contact__item-title {
	font-weight: 400;
	margin-bottom: 2px;
}

.airline-contact__item-value {
	font-weight: 500;
}





 


.airline-page__title{
	font-size: 24px;
	margin-bottom: 15px;
	font-weight: 600;
}

 
 


 



 @media(max-width:678px) {
 	.airline-contact__container {
 		padding-top: 15px;
 		padding-bottom: 15px;
		flex-direction: column;
 		 
 	}

 	.airline-contact__item {
 	 

 		margin-bottom: 20px;
 	}

 	.airline-contact__item:last-child {
 		margin-bottom: 0;
 	}
 }