 
ul{
    margin-bottom: 0;
}
h3{
    font-size: unset;
}
.modal-content{
    margin: 0 auto;
    max-width: 990px;
    min-height: 700px;
    width:100%; 
    padding: 20px 15px;
}
.modal-content__title{
    text-align: center;
color: #000000;
margin-bottom: 30px;

    font-family: 'Montserrat';
font-style: normal;
font-weight: 700;
font-size: 24px;
line-height: 29px;

}
.modal-content__description{
display: flex;
gap: 20px;
margin-bottom: 40px;
}


.modal-content__main-img{
     min-width: 300px;
     max-width: 300px;
    height:  180px;
    width: 100%; 
    border-radius: 15px;
}
.modal-content__img-container{
    display: flex;
    gap: 15px;
    justify-content: center;
    flex-wrap: wrap;
}
.modal-content__img  {
    width: 300px;
    height: 175px;
    border-radius: 10px;
}


.custom-badge{
    
    width: 20px;
    height: 20px;
display: flex;
justify-content: center;
align-items: center;
    border-radius: 100%;
    background-color: #18B2E2;
    font-size: 12px;
    color:white;
   
}
.modal-content__cards{
    display: flex;
    justify-content: space-between;
    align-items: center; 
    margin-bottom: 40px;
    flex-wrap: wrap;
    gap:10px;
}
.modal-content__card-list{
    display: flex;
    flex-direction: column;
    max-width: 275px;
    width: 100%;
   border-radius: 10px;
   padding-bottom: 30px;
   background-color: white;
   border:1px solid silver;
}
.modal-content__card-list ul {
    padding-left: 0;
}
.modal-content__card-list-header{
   display: flex;
   justify-content: center;
   align-items: center; 
   gap: 15px;
    border-bottom: 1px solid #CCCCCC;
    font-family: 'Inter';
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 19px;

color: #000000;
}
.modal-content__card-list-header, .modal-content__card-list-item{
    min-height: 30px;
}

.modal-content__card-list-item{
   
    display: flex;
     
    align-items: center;
    padding-left: 10px;
    gap: 10px;
    border-bottom: 1px solid #CCCCCC;
}


.card-value{
    max-width: 275px;
    width: 100%;
    min-height: 145px;
    background-color:  #10A1C1;
    padding-top: 15px;
    padding-bottom: 39px;
    color: white;
    font-family: 'Montserrat';
   
    text-align: center;
    
    border-radius: 10px;

}
h3.card-value__title{
    margin-bottom: 25px;
    font-weight: 700;
     
    font-size: 18px;
    
    line-height: 22px;
}
.card-value__value{
    font-size: 36px;
line-height: 44px;
font-weight: 700;
}





.weather-card {
    max-width: 275px;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #27ae60;
    border-radius: 10px;
    color: white;
    padding: 0 12px 5px  12px  ;
     
    font-family: "Open-Sans";
  
    font-weight: 700;
  }
  .weather-card__top-part {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 50px;
  }
  .weather-card__title {
   
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    text-align: center; 
  }
  .weather-card__main-info {
    display: flex;
    gap: 5px;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }
  .weather-card__main-value{
  
font-size: 22px;
line-height: 30px;
  }
  .weather-card__main-icon {
    width: 30px;
    height: 30px;
  }
  
  .weather-card__bottom-part{
    display: flex;
    justify-content: space-between;
}
.weather-card__bottom-icon{
    width: 20px;
    height: 20px;
}
.card-wrapper{
    max-width: 275px;
    width:100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 15px;
}








@media (max-width:976px) {

    .modal-content__cards{

    }

    .card-wrapper{
        max-width: 100%;
        flex-direction: row;
        justify-content: center;
    }
}



@media (max-width:892px) {
    .modal-content__cards{
        justify-content: center;
        gap: 30px;
    }
     
    
    .modal-content__description{
        flex-direction: column;
    }
    .modal-content__main-img{
        max-width: 400px;
        margin: 0 auto;
    }
}
@media (max-width:370px) {
   
    .modal-content__img{
        width:265px;
    }
}