.airline-card {
	max-width: 1140px;
	width: 100%;
	border: 1px solid silver;
	background-color:white;
	margin: 0 auto;
	border-radius: 10px;
}

.airline-card__container {
	display: flex;
}

.airline-card__left-side {
	padding-left: 45px;
	padding-top: 35px;
	padding-bottom: 35px;
}

.airline-card__right-side {
	padding-right: 15px;
	padding-top: 25px;
}

.airline-card__left-side,
.airline-card__right-side {
	width: 50%;

}

.airline-card__item {
	width: 100%;
	max-width: 420px;
	min-height: 60px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #D3D3D3;
	font-size: 16px;
}

.airline-card__note {
	padding-top: 15px;
}

.airline-card__value {
	font-weight: 600;
}

.airline-card__airline {
	display: flex;
	align-items: center;
	gap: 10px;
	font-weight: 700;
	margin-bottom: 5px;
}

.airline-card__airline-title {
	font-size: 22px;
}

.airline-card__rating {
	margin-bottom: 15px;
}

.airline-card__img {
	display: block;
	max-width: 470px;
	width: 100%;
	height: 270px;
	border-radius: 10px;

}

.airline-card__rating-value {
	font-weight: 700;
	font-size: 14px;
	color: #4A4A4A;
}


.airline-card__rating-block{
	display: flex;
	gap: 10px;
}
.airline-logo{
	width: 30px;
	height: 30px;
}

@media(max-width:678px) {
	.container {
		max-width: 100%;
	}

	.airline-card__rating {
		padding-left: 4%;
	}

	.airline-card__note {
		padding-left: 15px;
		/* padding-bottom: 10px; */
	}

	.airline-card__left-side,
	.airline-card__right-side {
		padding: 0;
		padding-top: 10px;

		width: 100%;
	}

	.airline-card__left-side {
		padding-bottom: 35px;
	}

	.airline-card__container {
		flex-direction: column-reverse;
	}

	.airline-card__item {
		max-width: 100%;
	}

	 
	.airline-card__img {
		max-width: 93%;

		margin: 0 auto;
		object-fit: cover;
	}

	.airline-card__item {
		margin: 0;
	}
}

@media(max-width:448px){
	.airline-card__img {
 
		object-fit: fill;
	}

}