* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

a {
	color: inherit;
	text-decoration: none;
}

a:hover {
	color: inherit;
}

ul {
	list-style-type: none;
}


footer {
	position: relative;
	background-color: #6C6464;
	min-height: 330px;
	width: 100%;
	color: white;
}

.footer__container {
	position: relative;
	z-index: 10;
}

.pl-0 {
	padding-left: 0;
}

.footer__about-title {
	font-family: 'Poppins';
	font-weight: 700;
	font-size: 28px;
	margin-bottom: 15px;
}

.footer__standart-title {
	padding-top: 30px;
	font-family: 'Montserrat';
	font-weight: 700;
	font-size: 18px;
	margin-bottom: 15px;
}

.footer__contact-title {
	display: inline-block;
	padding-right: 10px;
}

.footer__list-item {
	font-family: 'Open Sans';
	font-weight: 600;
	font-size: 15px;
	margin-bottom: 10px;
}

.footer__list-item:last-child {
	margin-bottom: 0;
}

.footer__about {
	padding-top: 20px;
}

.footer__about-description {

	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	padding-bottom: 55px;
}
.footer__list-items{
	padding: 0;
}

.footer__contacts-title {
	font-size: 20px;
	font-weight: 600;

}

.footer__contacts {
	display: flex;
	flex-direction: column;
	gap: 15px;
	margin-bottom: 30px;
}

.footer__contacts-item {
	display: flex;
	gap: 10px;
	align-items: center;

}

.footer__contact-item-value {

	font-family: 'Montserrat';
	font-weight: 500;
	font-size: 16px;
}

.footer__social-links {
	display: flex;
	gap: 10px;
}

.social-link__icon {
	width: 30px;
	height: 30px;
	cursor: pointer;
}

.footer__decoration {
	position: absolute;
	bottom: 0;
}

.footer__copyright-text {
	width: 100%;
	position: absolute;
	bottom: 20px;
	font-family: 'PT Sans';
	font-weight: 700;
	font-size: 18px;
	text-align: center;

}

@media(max-width:575px) {

	.footer__about {
		margin-bottom: 20px;
	}

	.nav-list {
		width: 100%;
		border-radius: 10px;

		padding: 0 0 20px 10px;
		display: flex;
		flex-direction: column;
		background-color: white;
		color: black;
		margin-bottom: 20px;
	}
	.footer__about-description {
		padding-bottom: 0;
	}

	.footer__contacts {
		width: 100%;
		border-radius: 10px;

		padding: 0 0 20px 10px;

		background-color: white;
		color: black;

	}

	.footer-path {
		fill: black;
	}

	.footer__social-links {
		margin-bottom: 70px;
	}

	.footer__copyright-text {
		font-size: 13px;
		bottom: 5px;
	}
	.footer__social-links{
		justify-content: space-around;
	}

}