* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-family: 'Open Sans', sans-serif;
	font-weight: bold;
}
.ticket {
	margin: 0 auto;
	display: flex;
	position: relative;
	max-width: 812px;
	width: 100%;
	min-height: 250px;
	background-color: #080808;
	color: white;
	border-radius: 20px;
}
.ticket__stop-container{
	display: flex;
	gap: 10px;
}


.range {
	width: 100%;
	min-height: 8px;
	background: white;
	border-radius: 10px;
}

.btn-link {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	bottom: 15px;
	right: 27px;
	max-width: 140px;
	width: 100%;
	min-height: 40px;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	background-color: white;
	color:black;
}
.btn-link:hover{
	color:black;
}
.ticket__airline {
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 14px;
	left: 20px;
	flex-wrap: wrap;
	z-index: 10;
}
.ticket__airline-detail {
	display: flex;
}

.ticket__airline-name {
	display: block;
	font-size: 16px;
	font-weight: 700; 
	line-height: 33px;
	margin-bottom: 8px;
	margin-right: 10px;
}
.ticket__class {
	font-size: 14px;
}

.ticket__first-section,
.ticket__last-section {
	display: flex;
	max-width: 216px;
	width: 100%;
	height: 250px;
	flex-direction: column;

	align-items: center;
	justify-content: center;
}

.ticket__time-depart {
	font-size: 28px;
}
.ticket__date-depart {
	font-size: 12px;
}

.ticket__range {
	display: flex;
	justify-content: center;
	max-width: 386px;
	width: 100%;
	min-height: 250px;
	flex-direction: column;
}
.ticket__time-range {
	display: flex;
	max-width: 386px;
	width: 100%;
	justify-content: space-between;
	margin-bottom: 8px;
}

.stop {
	font-size: 14px;
}
.ticket__duration-fly {
	font-size: 18px;
}
.ticket__places {
	display: flex;
	justify-content: space-between;
	padding-top: 8px;
	position: relative;
	z-index: 10;
}
 

@media (max-width: 768px) {
	
	.ticket__duration-fly {
		font-size: 14px;
	}

	.ticket__first-section,
	.ticket__last-section {
		max-width: 150px;
	}
}

@media (max-width: 576px) {
	.ticket {
		justify-content: center;
		align-items: center;
		border-radius: 12px;
		max-width: 100%;
		padding: 0 15px;
	} 
	.ticket__time-range,
	.ticket__range {
		max-width: 100%;
		padding-top: 20px;
	}
	.ticket__first-section,
	.ticket__last-section {
		position: absolute;
		max-width: 60px;
		z-index: 1;
	}
	.ticket__time-range {
		justify-content: center;
	}
	.ticket__date-arrive,
	.ticket__date-depart {
		display: none;
	}
	.ticket__time-depart {
		font-size: 24px;
	}
	.ticket__range img {
		display: none;
	}
	.ticket__first-section {
		left: 3%;
		top: -5px;
	}
	.ticket__last-section {
		right: 3%;
		top: -5px;
	}
}

@media (max-width: 430px) {
	.stop {
		margin-right: 5px;
	}
	.stop:last-child {
		margin-right: 0;
	}
}
