 
/*  Start F.A.Q. Section */
.faq-section{
    position: relative;
    }
    
    
     .faq-section__container{
        max-width: 540px;
        margin: 0 auto; 
        padding-top: 100px;
        padding-bottom: 88px;
        font-family: 'Open-Sans';
     }
    
    
    
    
     
    
    .faq-section{
    background-color: #205086;
    color:white;
    min-height: 570px;
    font-family: 'Poppins';
    }
    
    
    .faq-section__title{
    
    font-weight: 700;
    font-size: 32px;
    line-height: 48px;
     
    }
    .faq-section__subtitle{  
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    }  .faq-section__title_mb{
    margin-bottom: 15px;
    } 
    .faq-section__questions_pt{
        padding-top:50px;
    }
    
    .faq-left-decoration, .faq-right-decoration{
        position: absolute;
    }
    
    
    .faq-left-decoration{
    
        bottom: 0;
        left: 0;
    
    }
    
    
    .faq-right-decoration{
    top: 0;
    right: 0;
    
        
    } 
    
    
    
    @media (max-width:576px) {
        
    
    .faq-section__container{
        padding-top: 50px;
    }
    
    .faq-section__header{
        padding-left: 20px;
        padding-right: 20px;
    }
    
    .faq-section__title{
        font-size: 24px;
    }
    .faq-section__subtitle  {
        font-size: 18px;
    }
    
    
    
    .faq-section__questions{
        padding-left: 20px;
        padding-right: 20px;
    }
    
    
    .faq-left-decoration{ 
    width:130px ;
    height:175px ;
    }
    
    .faq-right-decoration{
    
    width: 150px;
    
    }  
            
    }
        
        
     
    /*  End F.A.Q. Section */
        