* {
	font-family: "Open Sans", sans-serif;
	padding: 0;
	margin: 0;
}
.sub-menu {
	min-width: 320px;
	max-width: 320px;
	border: 1px solid silver;
	margin: 0 auto;
	padding: 15px 0;
	background-color: white;
	 

}
.container {
	max-width: 280px;
	padding: 0;
	margin: 0;
	margin: 0 auto;
}
.title {
	font-size: 15px;
	font-weight: 500;
	color: #212f3d;
}


.modal-hamburger{
	cursor: pointer;
 

}
.sub-menu__header{
	display: flex;
	justify-content: space-between; 
	align-items: center;
	padding-bottom: 10px;
	margin-bottom: 10px;
}



span.modal-close{
background-color: #f44336;
}
span.modal-close:nth-child(1){
	transform: translateY(8px) rotate(45deg);
}
span.modal-close:nth-child(2){
	opacity: 0;
}

.modal-close:nth-child(3){
	transform: translateY(-8px) rotate(-45deg);
}


.sub-menu__item {
	margin-bottom: 20px;
}

.sub-menu__item:last-child {
	margin-bottom: 0;
}

.btn{
	border: 1px solid silver;
	border-radius: 20px;
	font-size: 14px;
	background-color: white;
	color: black;
	min-width: 70px;
	min-height: 40px;
	padding: 5px;
}

.btn-active {
	color:white;
	background-color: #3498db;
}
.send-btn { 
	min-height: 50px;
	width: 100%;
	color: white;
	background-color: #3498db;
	border-radius: 5px;
}
