@media (max-width:740px) {
	.container{
		padding-left: 20px;
		padding-right: 20px;
		height: 100%;
	}
	
.sub-menu{
	max-width: unset;
	width: 100%;
	height: 100vh;
	position: fixed;
	left:0;
	top:0;
	z-index: 25;
	overflow-y: scroll;
	 
	
}
.send-btn-wrapper{
	padding-bottom: 20px;
}
.sub-menu__header{
	margin-bottom: 10px;
}
.container{
	max-width: unset;
}
}