.nav-menu{
    color: white;
    background-color: #3498DB;
    

}
.nav-menu__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 70px; 
    max-width: 1140px;
    width: 100%;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 0px;
}
.nav-menu__item:last-of-type{
    padding-left: 40px;
    padding-right: 10px;
}
.currency-select{
    color:white;
    background: transparent;
    border: none;
    font-weight: 700;
}

.currency-select option {
    
    background: #e74c3c;
    color: #fff; 
  }

.currency-label{
    display: none;
}
.logo{
    font-family: 'Poppins';
    font-weight: 700;
    font-size: 28px;
    line-height: 42px;
    margin: 0;
}
 
.nav-menu__items{
    margin: 0;
    display: flex;
    gap: 50px;

}
.nav-menu__item{
    font-family: 'Open Sans';
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;

}


/* Start burger-menu */
 
.hamburger-menu{
    display: none;
    cursor: pointer;
    position: absolute;
    right: 20px;
    z-index:15;

} 
 

.bar{
display: block;
width: 25px;
height: 3px;
margin:5px auto;
transition: all 0.3s ease-in-out;
background-color: white;
}


@media (max-width:876px) {

    .hamburger-menu{
        display: block;
    }
    .currency-select option  {
        background-color: #3498DB;
    }
    .currency-label{
        display: inline;
        padding-right: 20px;
    }
    .nav-menu__item:last-of-type{
        padding: 0;
    }
   
    .hamburger-menu.h-active .bar:nth-child(2){
        opacity: 0;
    }
 
    .hamburger-menu.h-active .bar:nth-child(1){
        transform: translateY(8px) rotate(45deg);
    }
    .hamburger-menu.h-active .bar:nth-child(3){
        transform: translateY(-8px) rotate(-45deg);
    }

    .nav-menu__items{
        position: absolute;
        left: -100%;
        top: 0;
        z-index: 10;
        gap: 20px;
        padding: 30px 0 30px 0;
        flex-direction: column;
        background-color: crimson;
        width: 100%;
        text-align: center;
        transition: 0.3s;
    }
    .nav-menu__item{
        font-size: 18px;
    }
    .nav-menu_active{
        
        left: 0;
        height: 100%;
        position: fixed;
        
        z-index: 15; 

    }
} 