 
.filter-field,
.filter-field-xs input {

	min-width: 8rem;

}

.filter-field,
.filter-field-s input {

	min-width: 10rem;

}

.table-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.search-title {
	font-size: 18px;
}

.p-datatable-header .bo {
	border-radius: 10px;
}

 
.airline-fleet {
	max-width: 1140px;
	width: 100%;
	background-color: #f8f9fa;
	border-radius: 10px;
	border: 1px solid silver;
	margin: 0 auto;
}

.airline-page .list-items{
	margin-bottom: 20px;
}

.data-table .p-datatable-header {
	border-top: none;
	padding-top: 10px;
	padding-bottom: 15px;
	/* border-radius: 10px 10px 0 0; */
}
.data-table .p-paginator-bottom{
border-radius: 0px 0px 10px 10px;
}

/* 
total card styles */


.total-card {
	width: 255px;
	min-height: 125px;
	background: #ffffff;
	border-radius: 5px;
	border: 1px solid silver;
	font-family: "Rubik", sans-serif;
	padding-top: 10px;
	padding: 15px;
}
 

.total-card__content {
	display: flex;
 
	padding-top: 10px;
}

.total-card__title {
	font-size: 18px;
}

.total-card__value {
	font-size: 36px;
	padding-top: 10px;
}

.total-card__img {
	min-width: 125px;
	min-height: 80px;
	background-image: url("../../../public/assets/images/fleet_ready.png");
	background-size: 100%;
	background-repeat: no-repeat;
}
.download-btns{
	display: flex;
	gap: 10px;
	justify-content: flex-end;
	padding: 10px 40px 0 0;
}

.download-btn{
	width: 45px;
	height: 45px;
	border-radius: 20px;
	background-color: white;
	display: flex;
	justify-content: center;
	align-items: center;
}

@media (max-width:575px) {
	
.download-btns{
	justify-content: center;
	padding-right: 0;
}

}